// –¥–ª—è –∑–≤—ñ—Ç—ñ–≤
export const formatDateTitle = (dayOrePariod, selectedDateString) => {
  if (dayOrePariod === 'oneDay') {
    return `–∑–∞ ${selectedDateString[0].split('-').reverse().join('-')}`;
  } else {
    return `–∑ ${selectedDateString[0]
      .split('-')
      .reverse()
      .join('-')} –ø–æ ${selectedDateString[1].split('-').reverse().join('-')}`;
  }
};
