import { Drawer, Form, InputNumber, Select, Input, Button, Radio } from 'antd';
import { useForm } from 'antd/es/form/Form';
import { categoryExpense } from 'assets/constants/mainConstans';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { addExpense, updateExpense } from 'redux/expense/expenseOperetion';
import { DateDescription, DateTitle } from './ExpenseContainer.styled';
import { selectExpenseSelected } from 'redux/expense/expenceSelector';
import { clearExpenseSelected } from 'redux/expense/expenseSlice';

const ExpenseContainer = ({
  setOpen,
  selectedPeriod,
  open,
  onCloseDrawerExpense,
}) => {
  const [form] = useForm();
  const [paymentMethod, setPaymentMethod] = useState(null);
  const dispatch = useDispatch();
  const updateExpenseInfo = useSelector(selectExpenseSelected);

  useEffect(() => {
    if (updateExpenseInfo) {
      setOpen(true);
      form.setFieldsValue({
        date: updateExpenseInfo.date
          ? updateExpenseInfo.date.split('T')[0]
          : selectedPeriod,
        category: updateExpenseInfo.category || '–Ü–Ω—à–µ',
        amount: updateExpenseInfo.amount || null,
        paymentForm: updateExpenseInfo.paymentForm || 'cash',
        bank: updateExpenseInfo.bank || '',
        description: updateExpenseInfo.description || '',
      });
      setPaymentMethod(updateExpenseInfo.paymentForm || 'cash');
    }
  }, [updateExpenseInfo, setOpen, form, selectedPeriod]);

  return (
    <>
      <Drawer
        title="–ü–æ—Ç–æ—á–Ω—ñ –≤–∏—Ç—Ä–∞—Ç–∏"
        placement="right"
        width={400}
        onClose={onCloseDrawerExpense}
        open={open}
      >
        <Form
          form={form}
          initialValues={{
            date: null,
            category: '–Ü–Ω—à–µ',
            amount: null,
            paymentForm: 'cash',
            bank: '',
            description: '',
          }}
          onFinish={async values => {
            if (updateExpenseInfo) {
              const expenseData = { id: updateExpenseInfo._id, values };
              await dispatch(updateExpense(expenseData)).then(async () => {
                dispatch(clearExpenseSelected());
                onCloseDrawerExpense();
                form.resetFields();
              });
            } else {
              if (values.paymentForm === 'cash') {
                values.bank = '';
              }
              values.date = selectedPeriod;
              await dispatch(addExpense(values)).then(() => {
                onCloseDrawerExpense();
                form.resetFields();
              });
            }
          }}
        >
          <DateTitle>
            –î–∞—Ç–∞ –≤–∏—Ç—Ä–∞—Ç: <DateDescription>{selectedPeriod}</DateDescription>
          </DateTitle>

          <Form.Item
            label="–ö–∞—Ç–µ–≥–æ—Ä—ñ—è –≤–∏—Ç—Ä–∞—Ç"
            name="category"
            rules={[{ required: true, message: '–í–∏–±–µ—Ä—ñ—Ç—å –∫–∞—Ç–µ–≥–æ—Ä—ñ—é' }]}
          >
            <Select>
              {categoryExpense.map(expense => (
                <Select.Option key={expense.category} value={expense.category}>
                  {expense.title}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            rules={[{ required: true, message: '–í–Ω–µ—Å—ñ—Ç—å —Å—É–º—É –≤–∏—Ç—Ä–∞—Ç' }]}
            label="–°—É–º–∞"
            name="amount"
          >
            <InputNumber style={{ width: '100%' }} placeholder="–í–∫–∞–∂—ñ—Ç—å —Å—É–º—É" />
          </Form.Item>
          <Form.Item
            label="–§–æ—Ä–º–∞ –æ–ø–ª–∞—Ç–∏"
            name="paymentForm"
            onChange={e => {
              if (e.target.value === 'cash') {
                form.setFieldsValue({ bank: '' });
              }
            }}
          >
            <Radio.Group
              onChange={e => {
                setPaymentMethod(e.target.value);
                if (e.target.value !== 'cashless') {
                  form.setFieldsValue({ bank: undefined });
                  form.resetFields(['bank', 'sum']);
                }
              }}
            >
              <Radio value="cash">–ì–æ—Ç—ñ–≤–∫–∞</Radio>
              <Radio value="cashless">–ë–µ–∑–≥–æ—Ç—ñ–≤–∫–æ–≤–∞</Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item
            rules={
              paymentMethod === 'cashless' && [
                { required: true, message: '–û–±–µ—Ä—ñ—Ç—å –±–∞–Ω–∫' },
              ]
            }
            label="–í–∏–±–µ—Ä—ñ—Ç—å –±–∞–Ω–∫"
            name="bank"
            style={{ width: 250 }}
          >
            <Select>
              <Select.Option value="PrivatBank">–ü—Ä–∏–≤–∞—Ç–ë–∞–Ω–∫</Select.Option>
              <Select.Option value="MonoBank">–ú–æ–Ω–æ–±–∞–Ω–∫</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="–û–ø–∏—Å –≤–∏—Ç—Ä–∞—Ç" name="description">
            <Input.TextArea
              placeholder="–ó–∞ –ø–æ—Ç—Ä–µ–±–æ—é –¥–æ–¥–∞–π—Ç–µ –æ–ø–∏—Å"
              autoSize={{
                minRows: 1,
                maxRows: 5,
              }}
            />
          </Form.Item>
          <div
            style={{
              width: '100%',
              display: 'flex',
              alignItems: 'center',
              flexDirection: 'column',
              gap: 8,
            }}
          >
            <Button
              style={{ width: '100%' }}
              type="primary"
              onClick={onCloseDrawerExpense}
              htmlType="button"
            >
              –í—ñ–¥–º—ñ–Ω–∏—Ç–∏
            </Button>
            <Button style={{ width: '100%' }} type="primary" htmlType="submit">
              –ó–±–µ—Ä–µ–≥—Ç–∏ –∑–º—ñ–Ω–∏
            </Button>
          </div>
        </Form>
      </Drawer>
    </>
  );
};

export default ExpenseContainer;
